}

blockquote {
  padding: 15px 40px;
  margin: 0 0 25px;
  position: relative;
  color: #2b40a3;
  font-size: 17px;
  font-weight: 400;
  line-height: 27px;
}

@media only screen and (max-width: 767px) {
  blockquote {
    padding: 15px;
