  }
}

@media only screen and (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media only screen and (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media only screen and (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.main,
.sidebar {
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 30px;
}

.main {
  width: calc(100% - 300px);
}

@media only screen and (max-width: 991px) {
  .main {
    width: 100%;
    margin-right: 0;
  }
}

.sidebar {
  width: 300px;
  position: relative;
}

@media only screen and (max-width: 991px) {
  .sidebar {
    width: 100%;
  }
}

.item {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

.content img {
  height: auto;
}

.content ul,
.content ol {
  margin: 0 0 25px;
  padding: 0 0 0 40px;
  list-style-type: none;
}

@media only screen and (max-width: 575px) {
  .content ul,
  .content ol {
    padding: 0;
  }
}

.content ul li,
.content ol li {
  padding-left: 40px;
  margin-bottom: 10px;
  position: relative;
}

.content ul li::before {
  content: ' ';
  position: absolute;
  width: 20px;
  height: 20px;
  top: 3px;
  left: 0;
  background-color: #43d495;
  border-radius: 3px;
}

.content ol {
  counter-reset: list;
}

.content ol li::before {
  content: ' ';
  position: absolute;
  counter-increment: list;
  content: counter(list) ".";
  height: 36px;
  top: -8px;
  left: 0;
  color: #43d495;
  font-size: 26px;
  font-weight: 700;
  line-height: 36px;
  text-align: center;
}

.section {
  margin-bottom: 50px;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 25px;
}

@media only screen and (max-width: 767px) {
  .section-header {
    flex-direction: column;
  }
}

.section-header h2 {
  padding: 4px 0;
  margin: 0;
}

@media only screen and (max-width: 767px) {
  .section-header h2 {
    margin: 0 0 15px;
  }
}

.screens-box {
  margin: 0 -30px 30px;
  padding: 30px;
  background-color: #3e454f;
}

.screens-box .swiper-pagination-bullets {
  padding-top: 15px;
  display: flex;
  justify-content: center;
}

.screens-box .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  background-color: #fff;
  opacity: 1;
  border-radius: 10px;
  transition: all 0.3s ease-out;
}

.screens-box .swiper-pagination-bullet.swiper-pagination-bullet-active {
  width: 30px;
  height: 10px;
  background-color: #43d495;
}

.breadcrumbs-wrap {
  background-image: url("../img/breadcrumbs.png");
  background-color: #43d495;
  background-position: center top;
  background-repeat: no-repeat;
  margin-bottom: 50px;
}

.breadcrumbs {
  padding: 22px 0;
  font-size: 18px;
  line-height: 26px;
  color: #fff;
}

.breadcrumbs a {
  color: #fff;
  text-decoration: none;
}

.breadcrumbs a:hover {
  color: #3754dc;
}

.breadcrumbs .current-item {
  color: #3e454f;
}

.pagination {
  padding: 20px 0;
  display: block;
}

.pagination ul {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}

.pagination li {
  display: inline-block;
  height: 40px;
  text-align: center;
  position: relative;
}

.pagination li:not(:first-child):not(:last-child):not(:nth-last-child(2)) {
  position: relative;
}

.pagination li:not(:first-child):not(:last-child):not(:nth-last-child(2))::before {
  position: absolute;
  content: '';
  width: 35px;
  height: 2px;
  top: 18px;
  right: -17px;
  background-color: #e6e6e6;
  transform: rotate(-70deg);
}

.pagination a,
.pagination span {
  display: block;
  width: 40px;
  height: 100%;
  color: #3e454f;
  font-size: 22px;
  border-radius: 4px;
  text-decoration: none;
  margin: 0 1px;
  position: relative;
}

[data-theme=dark] .pagination a, [data-theme=dark]
.pagination span {
  color: #fff;
}

.pagination a:not(.prev), .pagination a:not(.next),
.pagination span:not(.prev),
.pagination span:not(.next) {
  display: inline-block;
  font-size: 18px;
  line-height: 40px;
}

@media only screen and (max-width: 550px) {
  .pagination a:not(.prev), .pagination a:not(.next),
  .pagination span:not(.prev),
  .pagination span:not(.next) {
    width: 30px;
    font-size: 24px;
  }
}

@media only screen and (max-width: 430px) {
  .pagination a:not(.prev), .pagination a:not(.next),
  .pagination span:not(.prev),
  .pagination span:not(.next) {
    width: 25px;
    font-size: 22px;
  }
}

.pagination a:hover {
  color: #000;
  font-weight: 700;
  text-decoration: none;
}

[data-theme=dark] .pagination a:hover {
  color: #8e9eeb;
}

.pagination a.prev, .pagination a.next {
  border: 2px solid #e6e6e6;
  border-radius: 10px;
}

.pagination a.prev::before, .pagination a.prev::after, .pagination a.next::before, .pagination a.next::after {
  position: absolute;
  content: '';
  width: 10px;
  height: 3px;
  left: 50%;
  background-color: #3e454f;
}

[data-theme=dark] .pagination a.prev::before, [data-theme=dark] .pagination a.prev::after, [data-theme=dark] .pagination a.next::before, [data-theme=dark] .pagination a.next::after {
  background-color: #fff;
}

.pagination a.prev::before, .pagination a.next::before {
  top: 19px;
}

.pagination a.prev::after, .pagination a.next::after {
  top: 14px;
}

.pagination a.prev:hover, .pagination a.next:hover {
  color: #fff;
  box-shadow: none;
}

[data-theme=dark] .pagination a.prev:hover, [data-theme=dark] .pagination a.next:hover {
  color: #8e9eeb;
}

.pagination a.prev:hover::before, .pagination a.prev:hover::after, .pagination a.next:hover::before, .pagination a.next:hover::after {
  background-color: #fff;
}

[data-theme=dark] .pagination a.prev:hover::before, [data-theme=dark] .pagination a.prev:hover::after, [data-theme=dark] .pagination a.next:hover::before, [data-theme=dark] .pagination a.next:hover::after {
  background-color: #8e9eeb;
}

.pagination a.prev::before {
  transform: translateX(-50%) rotate(45deg);
}

.pagination a.prev::after {
  transform: translateX(-50%) rotate(-45deg);
}

.pagination a.next::before {
  transform: translateX(-50%) rotate(-45deg);
}

.pagination a.next::after {
  transform: translateX(-50%) rotate(45deg);
}

.pagination a.prev span,
.pagination a.next span {
  display: none;
}

.pagination .current {
  color: #000;
  text-decoration: none;
  font-weight: 700;
}

.btn {
  display: inline-block;
  padding: 14px 35px;
  font-size: 16px;
  font-weight: 700;
  line-height: 22px;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  border: 0;
  border-radius: 25px;
  transition: all 0.3s ease-out;
  cursor: pointer;
  position: relative;
}

.btn-white {
  color: #2b40a3;
  background-color: #fff;
}

.btn-white::before, .btn-white::after {
  background: rgba(255, 255, 255, 0.1);
}

.btn-blue {
  color: #fff;
  background-color: #3754dc;
}

.btn-blue::before, .btn-blue::after {
  background: rgba(55, 84, 220, 0.1);
}

.btn-orange {
  color: #fff;
  background-color: #f67f4e;
}

.btn-orange::before, .btn-orange::after {
  background: rgba(246, 127, 78, 0.1);
}

.btn-white,
.btn-blue,
.btn-orange {
  margin: 10px;
  z-index: 1;
}

.btn-white::before, .btn-white::after,
.btn-blue::before,
.btn-blue::after,
.btn-orange::before,
.btn-orange::after {
  position: absolute;
  content: '';
  border-radius: 30px;
  z-index: -1;
}

.btn-white::before,
.btn-blue::before,
.btn-orange::before {
  border-radius: 45px;
  top: -10px;
  right: -10px;
  bottom: -10px;
  left: -10px;
  opacity: 0;
  transition: all 0.3s ease-out;
}

.btn-white::after,
.btn-blue::after,
.btn-orange::after {
  top: -10px;
  right: -10px;
  bottom: -10px;
  left: -10px;
  border-radius: 35px;
}

.btn-white:hover::before, .btn-white:focus::before, .btn-white:active::before,
.btn-blue:hover::before,
.btn-blue:focus::before,
.btn-blue:active::before,
.btn-orange:hover::before,
.btn-orange:focus::before,
.btn-orange:active::before {
  top: -20px;
  right: -20px;
  bottom: -20px;
  left: -20px;
  opacity: 1;
}

.btn-border,
.btn-site {
  width: 130px;
  line-height: 21px;
}

.btn-border {
  padding: 10px 20px;
  color: #3e454f;
  background-color: #fff;
  border: 2px solid #e6e6e6;
}

[data-theme=dark] .btn-border {
  color: #fff;
  background-color: #3e454f;
}

.btn-site {
  padding: 12px 20px;
  color: #fff;
  background-color: #f67f4e;
}

.btn-more {
  padding: 7px 26px;
  color: #777;
  font-size: 14px;
  text-transform: none;
  border: 2px solid #e6e6e6;
}

[data-theme=dark] .btn-more {
  color: #aaaaaa;
}

.btn-big {
  padding: 15px 42px;
}

@media only screen and (max-width: 1259px) {
  .btn-big {
    padding: 15px 30px;
  }
}

.btn-fullwidth {
  width: 100%;
  text-align: center;
}

.menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.menu li:not(:last-child) {
  margin-right: 10px;
}

.menu li a {
  text-decoration: none;
}

.header-menu,
.footer-menu {
