.menu-toggle {
  display: none;
  width: 45px;
  height: 45px;
  position: relative;
  cursor: pointer;
  margin-left: 20px;
  background-color: #f3f3f3;
  border: 2px solid #e6e6e6;
  border-radius: 10px;
}

@media only screen and (max-width: 991px) {
  .menu-toggle {
    display: block;
    order: 4;
  }
}

@media only screen and (max-width: 575px) {
  .menu-toggle {
    margin-left: 15px;
    width: 40px;
    height: 40px;
  }
}

.menu-toggle::before, .menu-toggle::after {
  position: absolute;
  content: '';
  height: 2px;
  left: 8px;
  top: 50%;
  background-color: #777;
  transform: translateY(-50%);
  transition: all .2s ease;
}

@media only screen and (max-width: 575px) {
  .menu-toggle::before, .menu-toggle::after {
    left: 6px;
  }
}

.menu-toggle::before {
  width: 24px;
  margin-top: -3px;
}

.menu-toggle::after {
  width: 16px;
  margin-top: 3px;
}

.menu-toggle.on::before, .menu-toggle.on::after {
  margin-top: 0;
}

.menu-toggle.on::before {
  transform: translateY(-50%) rotate(45deg);
}

.menu-toggle.on::after {
  width: 24px;
  transform: translateY(-50%) rotate(-45deg);
}

#popup {
  background-color: #fff;
  padding: 20px 30px;
  text-align: left;
  width: 100%;
  max-width: 400px;
  border-radius: 5px;
  margin: 40px auto;
