#popup textarea {
  padding: 15px;
  max-width: 100%;
  max-height: 200px;
  background-color: #e2e2e2;
  border: 2px solid #3754dc;
  border-radius: 5px;
  resize: none;
  margin-bottom: 10px;
}

#popup .ajax-loader {
