#popup .popup-descr {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 20px;
}

#popup .popup-item {
  padding-left: 40px;
  position: relative;
  color: #fff;
  font-size: 18px;
  margin-bottom: 20px;
  display: flex;
