.section-slots-single .main-game-frame.fullscreen .main-game-frame-box,
.section-slots-single .main-game-frame.fullscreen iframe {
  flex-grow: 1;
}

.section-slots-single .main-game-frame.fullscreen .game-frame-nav-maximize {
  background-image: url("../img/nofullscreen.png");
}

.section-slots-single .post-content {
  padding: 30px;
  background-color: #fff;
  border: 2px solid #e6e6e6;
  border-radius: 10px;
}

[data-theme=dark] .section-slots-single .post-content {
  background-color: #545e6c;
  border-color: #aaaaaa;
}

.section-slots-single .post {
  margin-bottom: 30px;
}

.item-news-wrap {
  width: 100%;
}

.item-news {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.item-news-img {
  width: 25%;
  display: block;
  border-radius: 10px 0 0 10px;
  overflow: hidden;
}

@media only screen and (max-width: 1259px) {
  .item-news-img {
    width: 50%;
  }
}

@media only screen and (max-width: 767px) {
  .item-news-img {
    width: 100%;
    border-radius: 10px 10px 0 0;
  }
}

.item-news-text {
  padding: 15px 15px 15px 30px;
  width: 75%;
  background-color: #fff;
  border: 2px solid #e6e6e6;
  border-left-width: 0;
  border-radius: 0 10px 10px 0;
}

@media only screen and (max-width: 1259px) {
  .item-news-text {
    width: 50%;
  }
}

@media only screen and (max-width: 767px) {
  .item-news-text {
    padding: 20px 15px;
    width: 100%;
    border-left-width: 2px;
    border-top-width: 0;
    border-radius: 0 0 10px 10px;
  }
}

[data-theme=dark] .item-news-text {
  background-color: #545e6c;
  border-color: #aaaaaa;
}

.item-news-title {
  display: inline-block;
  color: #0b122e;
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
  margin-bottom: 10px;
}

[data-theme=dark] .item-news-title {
  color: #fff;
}

.item-news-title:hover {
  color: #3754dc;
}

[data-theme=dark] .item-news-title:hover {
  color: #8e9eeb;
}

.item-news-descr {
  font-size: 16px;
}

.item-news-descr a {
  color: #a9a6a6;
  text-decoration: none;
}

.item-news-descr a:hover {
  color: #3754dc;
}

.item-news-descr *:last-child {
  margin-bottom: 0;
}
