.pagination {
	padding: 20px 0;
	display: block;

	ul {
		padding: 0;
		margin: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		list-style-type: none; }

	li {
		display: inline-block;
		height: 40px;
		text-align: center;
		position: relative;

		&:not(:first-child):not(:last-child):not(:nth-last-child(2)) {
			position: relative;

			&::before {
				position: absolute;
				content: '';
				width: 35px;
				height: 2px;
				top: 18px;
				right: -17px;
				background-color: $color-border;
				transform: rotate(-70deg); } } }

	a,
	span {
		display: block;
		width: 40px;
		height: 100%;
		color: $color-text;
		font-size: 22px;
		border-radius: 4px;
		text-decoration: none;
		margin: 0 1px;
		position: relative;
		@include nm {
			color: $color-white; }

		&:not(.prev),
		&:not(.next) {
			display: inline-block;
			font-size: 18px;
			line-height: 40px;
			@include r(550) {
				width: 30px;
				font-size: 24px; }
			@include r(430) {
				width: 25px;
				font-size: 22px; } } }

	a {

		&:hover {
			color: #000;
			font-weight: 700;
			text-decoration: none;
			@include nm {
				color: lighten($color-accent, 20%); } }

		&.prev,
		&.next {
			border: 2px solid $color-border;
			border-radius: 10px;

			&::before,
			&::after {
				position: absolute;
				content: '';
				width: 10px;
				height: 3px;
				left: 50%;
				background-color: $color-text;
				@include nm {
					background-color: $color-white; } }

			&::before {
				top: 19px; }

			&::after {
				top: 14px; }

			&:hover {
				color: #fff;
				box-shadow: none;
				@include nm {
					color: lighten($color-accent, 20%); }

				&::before,
				&::after {
					background-color: #fff;
					@include nm {
						background-color: lighten($color-accent, 20%); } } } }

		&.prev {

			&::before {
				transform: translateX(-50%) rotate(45deg); }

			&::after {
				transform: translateX(-50%) rotate(-45deg); } }

		&.next {

			&::before {
				transform: translateX(-50%) rotate(-45deg); }

			&::after {
				transform: translateX(-50%) rotate(45deg); } }

		&.prev span,
		&.next span {
			display: none; } }

	.current {
		color: #000;
		text-decoration: none;
		font-weight: 700; } }
